"use client"

import { useState, useEffect } from 'react'
import { Badge } from '@/components/ui/badge'

interface TickerItem {
  id: string
  text: string
  type: 'update' | 'new' | 'modified'
  priority: 'high' | 'medium' | 'low'
}

export function TickerBanner() {
  const [tickerItems, setTickerItems] = useState<TickerItem[]>([])
  const [currentIndex, setCurrentIndex] = useState(0)

  useEffect(() => {
    // Simulate fetching ticker data
    const mockTickerItems: TickerItem[] = [
      {
        id: '1',
        text: 'NEW: Updated Army Regulation 25-2 - Information Management and Information Technology',
        type: 'new',
        priority: 'high'
      },
      {
        id: '2',
        text: 'MODIFIED: Security Procedures Manual - Section 3.2 CAC Authentication updated',
        type: 'modified',
        priority: 'medium'
      },
      {
        id: '3',
        text: 'UPDATE: Network Operations Center contact information has been revised',
        type: 'update',
        priority: 'low'
      },
      {
        id: '4',
        text: 'NEW: Emergency Response Protocol v2.1 now available in document library',
        type: 'new',
        priority: 'high'
      }
    ]
    setTickerItems(mockTickerItems)
  }, [])

  useEffect(() => {
    if (tickerItems.length === 0) return

    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % tickerItems.length)
    }, 4000) // Change every 4 seconds

    return () => clearInterval(interval)
  }, [tickerItems.length])

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'new': return 'default'
      case 'modified': return 'secondary'
      case 'update': return 'outline'
      default: return 'outline'
    }
  }

  const getPriorityIndicator = (priority: string) => {
    switch (priority) {
      case 'high': return 'ðŸ”´'
      case 'medium': return 'ðŸŸ¡'
      case 'low': return 'ðŸŸ¢'
      default: return ''
    }
  }

  if (tickerItems.length === 0) {
    return (
      <div className="bg-muted/50 border-b">
        <div className="container mx-auto px-4 py-2">
          <div className="flex items-center gap-2">
            <Badge variant="outline" className="text-xs">LOADING</Badge>
            <div className="h-4 bg-muted rounded animate-pulse flex-1 max-w-md"></div>
          </div>
        </div>
      </div>
    )
  }

  const currentItem = tickerItems[currentIndex]

  return (
    <div className="bg-muted/50 border-b overflow-hidden">
      <div className="container mx-auto px-4 py-2">
        <div className="flex items-center gap-3 animate-in slide-in-from-right-full duration-500">
          <div className="flex items-center gap-2 flex-shrink-0">
            <Badge variant={getTypeColor(currentItem.type)} className="text-xs font-medium">
              {currentItem.type.toUpperCase()}
            </Badge>
            <span className="text-sm">{getPriorityIndicator(currentItem.priority)}</span>
          </div>
          <div className="flex-1 min-w-0">
            <p className="text-sm text-foreground truncate md:whitespace-normal">
              {currentItem.text}
            </p>
          </div>
          <div className="flex-shrink-0 text-xs text-muted-foreground">
            {currentIndex + 1} / {tickerItems.length}
          </div>
        </div>
      </div>
    </div>
  )
}
